
#options input,
#options select {
  box-shadow: 2px 1px 2px 1px rgba(0, 0, 0, 0.1);
  border: solid 1px #ccc;
  border-radius: 5px;
}
#options select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;
  background-position: right;
}
#options input[type="checkbox"] {
  opacity: 0;
  height: 1.5em;
  width: 1.5em;
  position: relative;
  right: -1.8em;
  z-index: 1;
}
#options input[type="checkbox"] + label {
  text-align: center;
}
#options input[type="checkbox"] + label div {
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  display: inline-block;
  line-height: 1.5em;
  font-size: 0.75em;
  height: 1.5em;
  width: 1.5em;
  position: relative;
  top: -0.5em;
  margin: 0 4px 0 0;
  color: transparent;
  background-color: #fff;
  box-shadow: 2px 1px 2px 1px rgba(0, 0, 0, 0.1);
  border: solid 1px #ccc;
  border-radius: 5px;
}
#options input[type="checkbox"]:checked + label div {
  color: black;
}
#options input[type=number] {
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield;
}
#options .group {
  font-weight: bold;
  margin-bottom: 0.9em;
}
#options .group.inner {
  padding-left: 2em;
}
#options .group.hidden {
  display: none;
}
#options .name {
  color: #ffd;
  background: #444;
  margin-bottom: 0.5em;
  padding: 0.3em 0.5em;
  position: sticky;
}
#options .subname {
  margin: 0.3em 0.5em;
  border-bottom: solid 1px #bbb;
}
#options .row {
  font-size: 12pt;
  font-weight: normal;
  height: 1.8em;
  padding: 0.2em 0.5em;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}
#options .row .cell {
  display: inline-block;
}
#options .row .cell span {
  padding: 0 0.2em;
}
#options .row .row-label {
  display: inline-block;
  white-space: pre;
}
#options .row .value {
  width: 4em;
  text-align: right;
}
#options .row input.value[type="color"] {
  width: 4.5em;
}
#options .row select.value,
#options .row input.value[type="text"] {
  width: 10em;
  text-align: left;
  height: 100%;
}
#options .row input.value[type="number"] {
  text-align: center;
}
#options .row input[type="radio"] {
  margin: 0;
  padding: 0;
  height: 24px;
}



